    constructor(private localTripSheetRpt: ReportService, private router: Router,
        private masterReadService: MasterReadService, private invoice: Invoice,
        private modalService: NgbModal) {
        if (sessionStorage.length == 0) {
            this.isLoggedIn = false;
            swal({
                title: "Session Expired",
                text: "Please relogin to access the application!",
                icon: "error",
                closeOnClickOutside: false,
                closeOnEsc: false,
            }).then(() => {
                this.logInPage();
            })
        }
        if (this.isLoggedIn) {
            this.userDataDtoReturnSession = JSON.parse(sessionStorage.getItem('SRDWeb'));
            this.address = this.userDataDtoReturnSession.addressId == null ? '' : this.userDataDtoReturnSession.addressId;
            this.getConsigneeDetails();
            this.getBranchAreaDetailsList();
            this.getVehicleMasterList();
            this.localTripSheetRptTable = true;
        }
    }

